body {
  color: #050505;
  background-color: #f8f8f8;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

p:last-child {
  margin-bottom: 0;
}

input, button, textarea, select {
  font-family: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

video {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 100px;
  }
}





:root {
  --color-brand: #9bb537;
  --transform-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.hero-container {
  padding-top: 64px;
  padding-bottom: 40px;
  padding-left: 20px;
}

.hero-content {
  padding-right: 20px;
}

.hero-title {
  text-transform: uppercase;
  margin-bottom: 14px;
  font-size: 26px;
  font-weight: 600;
  line-height: 1.2;
}

.hero-title-decor {
  color: var(--color-brand);
  text-transform: uppercase;
  font-style: italic;
  font-weight: 400;
}

.hero-text {
  margin-bottom: 28px;
  font-weight: 400;
}

.hero-btn {
  border: 1px solid var(--color-brand);
  text-align: center;
  transition: background-color var(--transform-dur-and-func), border var(--transform-dur-and-func);
  border-radius: 12px;
  margin-bottom: 48px;
  padding: 10px 20px;
  font-weight: 500;
  line-height: normal;
}

.hero-btn:hover, .hero-btn:focus {
  background-color: var(--color-brand);
  border: 1px solid rgba(0, 0, 0, 0);
}

@media screen and (min-width: 768px) {
  .hero-container {
    padding-left: 32px;
  }

  .hero-content {
    padding-right: 32px;
  }

  .hero-title {
    text-transform: uppercase;
    max-width: 446px;
    margin-bottom: 16px;
    font-size: 36px;
  }

  .hero-text {
    max-width: 408px;
    margin-bottom: 32px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
  }

  .hero-btn {
    text-align: center;
    transition: background-color var(--transform-dur-and-func), border var(--transform-dur-and-func);
    border-radius: 15px;
    margin-bottom: 64px;
    padding: 14px 28px;
  }
}

@media screen and (min-width: 1280px) {
  .hero-container {
    flex-wrap: nowrap;
    padding-top: 60px;
    padding-bottom: 50px;
    padding-left: 100px;
    display: flex;
  }

  .hero-content {
    flex: 0 0 446px;
    margin-right: 32px;
    padding-right: 0;
  }

  .hero-title {
    margin-top: 64px;
  }

  .hero-btn {
    margin-bottom: 0;
  }
}

.slider-container {
  max-width: 497px;
  margin-left: 0;
  overflow: hidden;
}

.swiper {
  width: 497px;
  height: 302px;
}

.slider-images {
  gap: 16px;
  display: flex;
}

.image-container-1, .image-container-2, .image-container-3 {
  height: 280px;
  border-radius: 15px;
  overflow: hidden;
}

.image-1 {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-3 {
  max-width: 120%;
  height: 120%;
  object-fit: cover;
}

.image-2 {
  width: 157px;
  height: 142px;
}

.image-container-1 {
  width: 80px;
}

.image-container-2, .image-container-3 {
  width: 200px;
  background: #050505;
}

.image-container-2 {
  box-sizing: border-box;
  border: 1px solid rgba(155, 181, 55, .3);
  flex-direction: column;
  justify-content: end;
  align-items: center;
  display: flex;
}

.image-title {
  color: rgba(248, 248, 248, .6);
  text-align: center;
  text-transform: uppercase;
  margin-top: 20px;
  margin-bottom: 8px;
  font-size: 10px;
  font-weight: 500;
  line-height: 1.4;
}

p.image-descraption {
  color: rgba(248, 248, 248, .2);
  text-align: center;
  margin-bottom: 14px;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.5;
}

.swiper .swiper-pagination {
  transform: translate(-11.5%, 58%);
}

.swiper .swiper-pagination-bullet {
  width: 5px;
  height: 5px;
  opacity: 1;
  background: #050505;
}

.swiper .swiper-pagination-bullet-active {
  background: var(--color-brand);
  opacity: 1;
}

@media screen and (min-width: 768px) {
  .slider-container {
    max-width: 871px;
  }

  .swiper {
    width: 871px;
    height: 468px;
  }

  .image-container-1, .image-container-2, .image-container-3 {
    height: 442px;
  }

  .image-container-1 {
    width: 137px;
  }

  .image-container-2, .image-container-3 {
    width: 351px;
  }

  .image-2 {
    width: 304px;
    height: 271px;
  }

  .image-title {
    color: rgba(248, 248, 248, .6);
    margin-top: 32px;
    font-size: 14px;
    line-height: 1.28571;
  }

  p.image-descraption {
    margin-bottom: 32px;
    font-size: 12px;
    line-height: 1.16667;
  }

  .swiper .swiper-pagination {
    transform: translate(-12%, 58%);
  }
}

@media screen and (min-width: 1280px) {
  .hero-slider {
    min-width: 0;
  }

  .slider-container {
    overflow: hidden;
  }
}

.footer-wrap {
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  padding-top: 60px;
  padding-bottom: 20px;
  display: flex;
}

.feedback-btn {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #9bb537;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-weight: 500;
  line-height: normal;
  display: inline-flex;
}

.feedback-btn:is(:hover, :focus) {
  color: #f8f8f8;
  background-color: #9bb537;
}

.footer-logo {
  letter-spacing: -.64px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.12;
}

.footer-logo-text {
  color: #9bb537;
}

.footer-social-list {
  justify-content: center;
  gap: 12px;
  display: flex;
}

.footer-social-link {
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-social-link:hover .footer-social-icon, .footer-social-link:focus .footer-social-icon {
  fill: #9bb537;
  transform: scale(1.1);
}

.footer-nav-link:is(:hover, :focus) {
  color: #9bb537;
}

.footer-social-icon {
  fill: #050505;
  width: 30px;
  height: 28px;
  display: inline-block;
}

.copyright {
  color: rgba(5, 5, 5, .5);
}

@media screen and (min-width: 768px) {
  .feedback-btn:nth-child(2) {
    order: -1;
  }

  .feedback-btn {
    padding: 14px 28px;
  }

  .footer-logo {
    letter-spacing: -.72px;
    font-size: 18px;
    line-height: 1.25;
  }

  .footer-social-icon {
    width: 32px;
    height: 32px;
  }

  .footer-nav-item, .copyright {
    font-size: 16px;
    line-height: 1.25;
  }

  .footer-wrap {
    flex-flow: wrap;
    justify-content: space-between;
  }

  .footer-nav-list {
    flex-direction: row;
    display: inline-flex;
  }

  .footer-nav-item:not(:first-child):before {
    content: "|";
  }
}

@media screen and (min-width: 1280px) {
  .footer-wrap {
    gap: 24px 80px;
  }
}

.btn-up {
  visibility: hidden;
  z-index: 3;
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #9bb537;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
  position: fixed;
  bottom: 60px;
  right: 26px;
  transform: translateX(100px);
}

.hidden {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}

.btn-up.hidden:is(:hover, :focus) {
  color: #f8f8f8;
  background-color: #9bb537;
}

.btn-up-icon {
  fill: #050505;
  width: 16px;
  height: 18px;
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .btn-up {
    width: 44px;
    height: 44px;
    bottom: 116px;
  }

  .btn-up-icon {
    width: 18px;
    height: 20px;
  }
}

@media screen and (min-width: 1280px) {
  .btn-up {
    bottom: 130px;
  }
}













.order-backdrop {
  width: 100%;
  height: 100%;
  background: var(--color-black-40, rgba(20, 20, 20, .5));
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.order-modal {
  width: 335px;
  background: #fff;
  border-radius: 15px;
  padding: 28px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.order-modal-close-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

.order-modal-close-btn:hover, .order-modal-close-btn:focus {
  border: none;
}

.order-modal-close-icon {
  width: 20px;
  height: 20px;
}

.order-modal-text {
  text-transform: uppercase;
  color: #050505;
  margin-bottom: 28px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.375rem;
}

.order-form {
  flex-direction: column;
  display: flex;
}

.order-form-field {
  margin-bottom: 8px;
}

.order-form-desc {
  color: rgba(5, 5, 5, .5);
  margin-bottom: 8px;
  display: block;
}

.order-form-input {
  width: 100%;
  height: 46px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(46, 47, 66, .4);
  border-radius: 15px;
  margin-bottom: 16px;
  padding: 14px 18px;
}

.order-form-input:focus {
  outline: none;
}

.order-form-message {
  width: 100%;
  min-height: 80px;
  max-height: 100px;
  resize: none;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(46, 47, 66, .4);
  border-radius: 15px;
  margin-bottom: 40px;
  padding: 14px 18px;
}

.order-form-message:focus {
  outline: none;
}

@media screen and (min-width: 768px) {
  .order-modal {
    width: 440px;
  }
}

